.nav-toggle-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: 60px;
  height: 60px;
  border: none;
  padding: 0;
  background-color: #fff;
  text-align: center;
  position: fixed;
  top: 8px;
  right: 5%;
}
@media only screen and (min-width: 768px) {
  .nav-toggle-btn {
    width: 70px;
    height: 70px;
  }
}
.nav-toggle-btn > span {
  display: inline-block;
  position: relative;
  height: 3px;
  width: 34px;
  border-radius: 1px;
  background: #222;
  vertical-align: middle;
}
.nav-toggle-btn > span:before, .nav-toggle-btn > span:after {
  display: inline-block;
  position: absolute;
  content: "";
  height: 3px;
  border-radius: 1px;
  background: #222;
  transition: all 200ms;
}
.nav-toggle-btn > span:before {
  top: -11px;
  left: 3px;
  width: 28px;
}
.nav-toggle-btn > span:after {
  top: 11px;
  left: 6px;
  width: 22px;
}
.nav-toggle-btn:focus {
  outline: none;
}
.nav-toggle-btn:hover > span:after, .nav-toggle-btn:hover > span:before {
  width: 34px;
  left: 0;
}

.nav-toggle-btn.active > span {
  height: 0;
}
.nav-toggle-btn.active > span:after, .nav-toggle-btn.active > span:before {
  width: 34px;
  top: 0px;
  left: 0;
}
.nav-toggle-btn.active > span:after {
  transform: rotate(-45deg);
}
.nav-toggle-btn.active > span:before {
  transform: rotate(45deg);
}

.nav-backdrop-container {
  position: fixed;
  background-color: rgba(27, 27, 28, 0.7);
  transform: translate(100%, -100%);
  transition: all 0.3s ease-in-out;
  display: flex;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
}

.nav-backdrop-container.active {
  transform: translate(0, 0);
}

